"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import {
  Sheet,
  SheetContent,
  SheetHeader,
  SheetTitle,
  SheetTrigger,
} from "@/components/ui/sheet"
import { Calendar, Heart, Phone, MessageSquare } from "lucide-react"
import ContactForm from "@/components/ContactForm"

interface MobileActionBarProps {
  agente: {
    nombre: string
    telefono: string
  }
  esFavorito?: boolean
  onToggleFavorito?: () => void
}

export default function MobileActionBar({
  agente,
  esFavorito = false,
  onToggleFavorito,
}: MobileActionBarProps) {
  const [showContactForm, setShowContactForm] = useState(false)
  const [showCalendar, setShowCalendar] = useState(false)

  return (
    <>
      <div className="fixed bottom-0 left-0 right-0 bg-white border-t border-gray-200 p-4 lg:hidden">
        <div className="grid grid-cols-4 gap-2">
          <Button
            variant="outline"
            size="icon"
            className="w-full"
            onClick={onToggleFavorito}
          >
            <Heart
              className={`w-5 h-5 ${esFavorito ? "fill-black text-black" : ""}`}
            />
          </Button>

          <Button
            variant="outline"
            size="icon"
            className="w-full"
            onClick={() => setShowCalendar(true)}
          >
            <Calendar className="w-5 h-5" />
          </Button>

          <Button
            variant="outline"
            size="icon"
            className="w-full"
            onClick={() => setShowContactForm(true)}
          >
            <MessageSquare className="w-5 h-5" />
          </Button>

          <Button
            className="w-full bg-black hover:bg-gray-800"
            size="icon"
            asChild
          >
            <a href={`tel:${agente.telefono}`}>
              <Phone className="w-5 h-5" />
            </a>
          </Button>
        </div>
      </div>

      {/* Modal de Contacto */}
      <Sheet open={showContactForm} onOpenChange={setShowContactForm}>
        <SheetContent side="bottom" className="h-[90vh]">
          <SheetHeader>
            <SheetTitle>Contactar al Agente</SheetTitle>
          </SheetHeader>
          <div className="mt-4">
            <ContactForm />
          </div>
        </SheetContent>
      </Sheet>

      {/* Modal de Calendario */}
      <Sheet open={showCalendar} onOpenChange={setShowCalendar}>
        <SheetContent side="bottom" className="h-[90vh]">
          <SheetHeader>
            <SheetTitle>Agendar Visita</SheetTitle>
          </SheetHeader>
          <div className="mt-4">
            <Calendar
              mode="single"
              className="rounded-md border"
            />
            <Button className="w-full mt-4 bg-black hover:bg-gray-800">
              Solicitar Visita
            </Button>
          </div>
        </SheetContent>
      </Sheet>
    </>
  )
}
