"use client"

import { useEffect, useRef } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { MapPin } from "lucide-react"

interface PropertyMapProps {
  lat: number
  lng: number
  title: string
  address: string
}

export default function PropertyMap({ lat, lng, title, address }: PropertyMapProps) {
  const mapRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    // En una implementación real, aquí se inicializaría el mapa de Google Maps
    // usando la API de Google Maps JavaScript
    if (mapRef.current) {
      const createMockMap = () => {
        const mockMap = document.createElement("div")
        mockMap.className = "w-full h-full bg-gray-100 rounded-lg flex items-center justify-center"
        mockMap.innerHTML = `
          <div class="text-center p-6">
            <div class="inline-flex items-center justify-center w-12 h-12 rounded-full bg-black text-white mb-4">
              <MapPin className="w-6 h-6" />
            </div>
            <div class="space-y-2">
              <h3 class="font-semibold">${title}</h3>
              <p class="text-sm text-gray-600">${address}</p>
              <p class="text-xs text-gray-500">Lat: ${lat}, Lng: ${lng}</p>
            </div>
          </div>
        `
        return mockMap
      }

      const mockMap = createMockMap()
      mapRef.current.innerHTML = ""
      mapRef.current.appendChild(mockMap)
    }
  }, [lat, lng, title, address])

  return (
    <Card>
      <CardHeader>
        <CardTitle className="flex items-center">
          <MapPin className="w-5 h-5 mr-2" />
          Ubicación
        </CardTitle>
      </CardHeader>
      <CardContent>
        <div className="space-y-4">
          <div className="flex items-start space-x-3">
            <MapPin className="w-5 h-5 text-gray-500 mt-1" />
            <div>
              <p className="font-medium">{title}</p>
              <p className="text-sm text-gray-600">{address}</p>
            </div>
          </div>
          
          <div ref={mapRef} className="aspect-[16/9] rounded-lg overflow-hidden">
            {/* El mapa se renderizará aquí */}
          </div>

          <div className="grid grid-cols-2 gap-4 text-sm">
            <div>
              <p className="text-gray-500">Latitud</p>
              <p className="font-medium">{lat}</p>
            </div>
            <div>
              <p className="text-gray-500">Longitud</p>
              <p className="font-medium">{lng}</p>
            </div>
          </div>
        </div>
      </CardContent>
    </Card>
  )
}
