"use client"

import { useState } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Calendar } from "@/components/ui/calendar"
import { Badge } from "@/components/ui/badge"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import { es } from "date-fns/locale"

// Datos de ejemplo
const reservas = [
  {
    id: 1,
    fechaInicio: new Date(2024, 1, 15),
    fechaFin: new Date(2024, 1, 20),
    estado: "confirmada",
    cliente: "Ana García",
  },
  {
    id: 2,
    fechaInicio: new Date(2024, 2, 1),
    fechaFin: new Date(2024, 2, 10),
    estado: "pendiente",
    cliente: "Carlos López",
  },
]

const mantenimiento = [
  {
    id: 1,
    fecha: new Date(2024, 1, 25),
    tipo: "limpieza",
  },
  {
    id: 2,
    fecha: new Date(2024, 2, 15),
    tipo: "reparación",
  },
]

interface DayInfo {
  date: Date
  isReserved: boolean
  isMaintenanceDay: boolean
  info?: {
    tipo: "reserva" | "mantenimiento"
    estado?: string
    cliente?: string
    descripcion?: string
  }
}

export default function PropertyCalendar() {
  const [selectedMonth, setSelectedMonth] = useState<Date>(new Date())
  const [selectedDay, setSelectedDay] = useState<DayInfo | null>(null)

  // Función para verificar si una fecha está dentro de una reserva
  const isDateReserved = (date: Date) => {
    return reservas.some(
      (reserva) =>
        date >= reserva.fechaInicio &&
        date <= reserva.fechaFin
    )
  }

  // Función para verificar si una fecha tiene mantenimiento programado
  const isMaintenanceDay = (date: Date) => {
    return mantenimiento.some(
      (m) =>
        m.fecha.getDate() === date.getDate() &&
        m.fecha.getMonth() === date.getMonth() &&
        m.fecha.getFullYear() === date.getFullYear()
    )
  }

  // Función para obtener la información de un día específico
  const getDayInfo = (date: Date): DayInfo => {
    const reserva = reservas.find(
      (r) =>
        date >= r.fechaInicio &&
        date <= r.fechaFin
    )

    if (reserva) {
      return {
        date,
        isReserved: true,
        isMaintenanceDay: false,
        info: {
          tipo: "reserva",
          estado: reserva.estado,
          cliente: reserva.cliente,
        },
      }
    }

    const mant = mantenimiento.find(
      (m) =>
        m.fecha.getDate() === date.getDate() &&
        m.fecha.getMonth() === date.getMonth() &&
        m.fecha.getFullYear() === date.getFullYear()
    )

    if (mant) {
      return {
        date,
        isReserved: false,
        isMaintenanceDay: true,
        info: {
          tipo: "mantenimiento",
          descripcion: mant.tipo,
        },
      }
    }

    return {
      date,
      isReserved: false,
      isMaintenanceDay: false,
    }
  }

  return (
    <Card>
      <CardHeader>
        <div className="flex items-center justify-between">
          <CardTitle>Calendario de Disponibilidad</CardTitle>
          <Select
            value={selectedMonth.getMonth().toString()}
            onValueChange={(value) => {
              const newDate = new Date(selectedMonth)
              newDate.setMonth(parseInt(value))
              setSelectedMonth(newDate)
            }}
          >
            <SelectTrigger className="w-[180px]">
              <SelectValue placeholder="Seleccionar mes" />
            </SelectTrigger>
            <SelectContent>
              {Array.from({ length: 12 }, (_, i) => (
                <SelectItem key={i} value={i.toString()}>
                  {new Date(2024, i, 1).toLocaleString("es", { month: "long" })}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
      </CardHeader>
      <CardContent>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div>
            <Calendar
              mode="single"
              selected={selectedDay?.date}
              onSelect={(date) => date && setSelectedDay(getDayInfo(date))}
              locale={es}
              modifiers={{
                reserved: (date) => isDateReserved(date),
                maintenance: (date) => isMaintenanceDay(date),
              }}
              modifiersStyles={{
                reserved: {
                  backgroundColor: "rgba(0, 0, 0, 0.1)",
                },
                maintenance: {
                  backgroundColor: "rgba(234, 179, 8, 0.1)",
                  color: "#854d0e",
                },
              }}
              className="rounded-md border"
            />
            <div className="mt-4 flex items-center gap-4">
              <div className="flex items-center gap-2">
                <div className="w-3 h-3 bg-black/10 rounded" />
                <span className="text-sm">Reservado</span>
              </div>
              <div className="flex items-center gap-2">
                <div className="w-3 h-3 bg-yellow-500/10 rounded" />
                <span className="text-sm">Mantenimiento</span>
              </div>
            </div>
          </div>

          <div>
            {selectedDay && selectedDay.info ? (
              <div className="space-y-4">
                <h3 className="font-semibold">
                  {selectedDay.date.toLocaleDateString("es", {
                    weekday: "long",
                    year: "numeric",
                    month: "long",
                    day: "numeric",
                  })}
                </h3>
                {selectedDay.info.tipo === "reserva" ? (
                  <>
                    <Badge
                      variant="outline"
                      className={
                        selectedDay.info.estado === "confirmada"
                          ? "bg-green-100 text-green-800 border-green-200"
                          : "bg-yellow-100 text-yellow-800 border-yellow-200"
                      }
                    >
                      {selectedDay.info.estado}
                    </Badge>
                    <div className="space-y-2">
                      <p className="text-sm text-gray-600">Cliente</p>
                      <p className="font-medium">{selectedDay.info.cliente}</p>
                    </div>
                  </>
                ) : (
                  <>
                    <Badge variant="outline" className="bg-yellow-100 text-yellow-800 border-yellow-200">
                      Mantenimiento
                    </Badge>
                    <div className="space-y-2">
                      <p className="text-sm text-gray-600">Tipo</p>
                      <p className="font-medium">{selectedDay.info.descripcion}</p>
                    </div>
                  </>
                )}
              </div>
            ) : (
              <div className="flex items-center justify-center h-full text-gray-500">
                Selecciona un día para ver más detalles
              </div>
            )}
          </div>
        </div>
      </CardContent>
    </Card>
  )
}
