"use client"

import { useState } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { Textarea } from "@/components/ui/textarea"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import { Star, ThumbsUp, MessageSquare } from "lucide-react"

// Datos de ejemplo
const reviews = [
  {
    id: 1,
    usuario: {
      nombre: "Ana García",
      avatar: "https://images.pexels.com/photos/1516680/pexels-photo-1516680.jpeg",
    },
    calificacion: 5,
    comentario:
      "Excelente propiedad, muy bien ubicada y con todas las comodidades necesarias. El agente fue muy profesional y atento durante todo el proceso.",
    fecha: "15 de enero, 2024",
    likes: 12,
    respuestas: 2,
  },
  {
    id: 2,
    usuario: {
      nombre: "Carlos López",
      avatar: "https://images.pexels.com/photos/2379005/pexels-photo-2379005.jpeg",
    },
    calificacion: 4,
    comentario:
      "Muy buena experiencia en general. La propiedad cumple con lo mostrado en las fotos. El único detalle fue que el proceso de documentación tomó más tiempo del esperado.",
    fecha: "10 de enero, 2024",
    likes: 8,
    respuestas: 1,
  },
]

function StarRating({ rating }: { rating: number }) {
  return (
    <div className="flex items-center">
      {[...Array(5)].map((_, i) => (
        <Star
          key={i}
          className={`w-4 h-4 ${
            i < rating ? "text-yellow-400 fill-current" : "text-gray-300"
          }`}
        />
      ))}
    </div>
  )
}

export default function PropertyReviews() {
  const [showReviewDialog, setShowReviewDialog] = useState(false)
  const [newReview, setNewReview] = useState("")
  const [selectedRating, setSelectedRating] = useState(0)

  const promedioCalificacion =
    reviews.reduce((acc, review) => acc + review.calificacion, 0) / reviews.length

  return (
    <div className="space-y-6">
      {/* Resumen de Calificaciones */}
      <Card>
        <CardContent className="p-6">
          <div className="flex items-center justify-between">
            <div>
              <h3 className="text-2xl font-bold mb-2">{promedioCalificacion.toFixed(1)}</h3>
              <StarRating rating={Math.round(promedioCalificacion)} />
              <p className="text-sm text-gray-500 mt-1">
                Basado en {reviews.length} reseñas
              </p>
            </div>
            <Dialog open={showReviewDialog} onOpenChange={setShowReviewDialog}>
              <DialogTrigger asChild>
                <Button className="bg-black hover:bg-gray-800">
                  Escribir Reseña
                </Button>
              </DialogTrigger>
              <DialogContent>
                <DialogHeader>
                  <DialogTitle>Escribir Reseña</DialogTitle>
                  <DialogDescription>
                    Comparte tu experiencia con esta propiedad
                  </DialogDescription>
                </DialogHeader>
                <div className="space-y-4 mt-4">
                  <div>
                    <label className="text-sm font-medium mb-2 block">
                      Calificación
                    </label>
                    <div className="flex gap-2">
                      {[1, 2, 3, 4, 5].map((rating) => (
                        <button
                          key={rating}
                          onClick={() => setSelectedRating(rating)}
                          className="focus:outline-none"
                        >
                          <Star
                            className={`w-6 h-6 ${
                              rating <= selectedRating
                                ? "text-yellow-400 fill-current"
                                : "text-gray-300"
                            }`}
                          />
                        </button>
                      ))}
                    </div>
                  </div>
                  <div>
                    <label className="text-sm font-medium mb-2 block">
                      Tu Reseña
                    </label>
                    <Textarea
                      value={newReview}
                      onChange={(e) => setNewReview(e.target.value)}
                      placeholder="Comparte tu experiencia..."
                      className="min-h-[100px]"
                    />
                  </div>
                  <Button
                    className="w-full bg-black hover:bg-gray-800"
                    onClick={() => setShowReviewDialog(false)}
                  >
                    Publicar Reseña
                  </Button>
                </div>
              </DialogContent>
            </Dialog>
          </div>
        </CardContent>
      </Card>

      {/* Lista de Reseñas */}
      <div className="space-y-4">
        {reviews.map((review) => (
          <Card key={review.id}>
            <CardContent className="p-6">
              <div className="flex items-start justify-between">
                <div className="flex items-start space-x-4">
                  <Avatar>
                    <AvatarImage src={review.usuario.avatar} />
                    <AvatarFallback>
                      {review.usuario.nombre.split(" ").map((n) => n[0]).join("")}
                    </AvatarFallback>
                  </Avatar>
                  <div>
                    <h4 className="font-semibold">{review.usuario.nombre}</h4>
                    <div className="flex items-center space-x-2">
                      <StarRating rating={review.calificacion} />
                      <span className="text-sm text-gray-500">
                        {review.fecha}
                      </span>
                    </div>
                  </div>
                </div>
              </div>
              <p className="mt-4 text-gray-600">{review.comentario}</p>
              <div className="flex items-center space-x-4 mt-4">
                <Button variant="ghost" size="sm">
                  <ThumbsUp className="w-4 h-4 mr-2" />
                  {review.likes}
                </Button>
                <Button variant="ghost" size="sm">
                  <MessageSquare className="w-4 h-4 mr-2" />
                  {review.respuestas}
                </Button>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>
    </div>
  )
}
